.page {
  scroll-behavior: smooth;
}

.page__body {
  min-width: 320px;
  margin: 0;
}

.page__body img {
  width: 100%;
}

.page__overlay, .page__menu {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.page__overlay {
  z-index: 2;
  opacity: 0;
  background-color: rgba(24, 24, 26, .5);
  height: 100vh;
  transition: opacity .3s ease-in-out;
}

.page__overlay:has(.page__menu:target) {
  opacity: 1;
}

.page__menu {
  transition: transform .3s ease-in-out;
  transform: translateY(-100%);
}

.page__menu:target {
  pointer-events: all;
  transform: translateY(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page ::-webkit-scrollbar {
  width: 15px;
}

.page ::-webkit-scrollbar-track {
  background: #e6e6e6;
}

.page ::-webkit-scrollbar-thumb {
